.wrapper {
  width: 70%;
  margin: 0 auto;
}

header {
  text-align: center;
  padding: 50px 10px;
}

header h1{
  font-size: 40px;
  font-weight: 800;
  color: rgb(243, 62, 101);
}


header p {
  color: #333;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1px;
  width: 200px;
  display: block;
  margin: 0 auto;
  margin-top: 30px;
}

main {
  display: flex;
}


@media screen and (max-width: 1100px) {
  main{
       flex-direction: column;
  }
}
@media screen and (max-width: 1100px) {
     main{
          flex-direction: column;
     }

     .nav-links ul{
          flex-direction: row;
     }
     
   }
.add-form {
     text-align: center;
     margin-bottom: 50px;
}

.add-form button {
     padding: 8px 25px;
     font-weight: 700;
     color:white;
     background-color: rgb(243, 62, 101);
     border: none;
     cursor: pointer;
     font-size: 18px;
     border-radius: 3px;
     margin-left: 30px;
     letter-spacing: 1px;
     outline: 0;
     transition: .3s;
}

.add-form input {
     width: 300px;
     padding: 8px 30px;
     font-weight: 400;
     color:#333;
     background-color: white;
     border: 3px solid rgb(243, 62, 101);
     font-size: 14px;
     border-radius: 3px;
     margin-left: 30px;
     letter-spacing: 1px;
     outline: 0;
}

.add-form button:hover { 
     transform: translate(10px);
}
.nav-links { 
     width: 20%;
}

.nav-links ul{
     display: flex;
     flex-direction: column;
}

.nav-links a{
     color: #333;
     margin-top:20px;
     padding: 5px 0;
     text-decoration: none;
     font-weight: 700;
     transition: .5s;
     border-color:  rgb(243, 62, 101);;
}

.nav-links a:hover{
     padding-left: 10px;
     color: rgb(243, 62, 101);
     
     border-radius: 2px;
     transform: translateX(0px);
}

.nav-links a.active {
     border-left: solid 20px rgb(243, 62, 101);
     padding-left: 10px;
}

@media screen and (max-width: 1100px) {
     .nav-links { 
          width: 100%;
     }
     

      .nav-links ul{
          flex-direction: row;
          margin-bottom: 40px;
          justify-content:space-around
     }

     .nav-links a{

     }

     .nav-links a:hover{
          padding-left: 0;
          color: rgb(243, 62, 101);
          border-radius: 2px;
     }
     
     .nav-links a.active {
          border-left: none;
          padding-left: 0;
          border-bottom: 4px solid rgb(243, 62, 101);
          margin-bottom: -4px;
     }
   }
.page {
     flex-grow: 1;
     padding: 15px;
     border-left: 15px solid rgb(243, 62, 101);
     border-radius: 5px;;
   }

.task-list{
    padding-left: 50px;
}

.element {
     display: flex;
     justify-content: space-between;
}

.task-list li {
     list-style: none;
     color: white;
     font-size: 16px;
     font-weight: 400;
     letter-spacing: 1px;
     color: #333;
     margin-bottom: 15px;
     max-width: 70%;
     margin-right: auto;
}

.task-list button{
     padding: 5px 10px;
     font-weight: 700;
     color:white;
     background-color: rgb(243, 62, 101);
     border: none;
     cursor: pointer;
     font-size: 12px;
     border-radius: 3px;
     margin-left: 20px;
     letter-spacing: 1px;
     outline: 0;
     transition: .3s;
     height: 25px;
}

.task-list button:hover { 
     transform: translate(5px);
}



.buttons{
     width: 25%;
}

@media screen and (max-width: 1100px) {
     .page {
          padding: 15px;
          border-left: none;
          border-top: 15px solid rgb(243, 62, 101)
      }

      .buttons{
          width: 40%;
     }
     
   }
.home-page h1{
     font-size: 24px;
     font-weight: 700;
     color: rgb(82, 82, 82);
     margin-bottom: 30px;
     letter-spacing: 1px;
     margin-left: 10vh;
}

.home-page p{
     color: #333;
     text-transform: uppercase;
     font-size: 14px;
     letter-spacing: 1px;
     width: 500px;
     margin-left: 10vh;
     margin-top: 10px;
}
* {
     margin: 0;
     padding: 0;
     box-sizing: border-box;
     font-family: 'Open Sans', sans-serif;
}
